import React from 'react';
import logo from '../Assets/CBLogo.png';
import {AppBar,Container, ListItem, MenuItem, Select, Toolbar,Typography} from "@material-ui/core";
import { makeStyles } from '@material-ui/core'; 
import SearchBar from './HomePage/SearchBar';
import man from "../Assets/man.png"; 
import {Link} from '@material-ui/core';


const Header = () => {
    
    const useStyles = makeStyles(()=>({
        App:{
            backgroundColor:"#071836" 
        },
        Title:{
            color:"#FFDF00",
            fontWeight: "bold",
            fontFamily: "Segoe UI",
            fontSize: "x-large" 
        },

         Logo:{
             width:"3.5%",
             marginLeft:"auto"
            
         },

           
    }));

    const classes=useStyles()


  return (
    <>
    <AppBar position='sticky' className={classes.App}>
    <Container> 
        
        <Toolbar className={classes.Nav}>
            
           <Link href="/Crypto-Board"><Typography className={classes.Title} >CryptoBoard</Typography> </Link>
             
            <img src={man} className={classes.Logo}/>

        </Toolbar>
    </Container>
    
    </AppBar>
    </>
  )
}

export default Header
